import { request, requestDownload } from 'utils';

const prefix = '/api/organization';
export const getUser = () =>
  request({
    prefix: '/identity',
    url: `/sign-in`,
    data: { username: 'admin', password: 'admin' },
    method: 'POST',
  });

export const getSalaryFrequencyConfig = paramsSearch =>
  request({
    prefix,
    url: `/SalaryFrequencyConfig`,
    params: { ...paramsSearch },
    method: 'GET',
  });

export const createSalaryFrequencyConfig = (newItem, token, headers = {}) =>
  request({
    prefix,
    url: `/SalaryFrequencyConfig`,
    headers: {
      Accept: 'application/json',
      'Content-Type': 'application/json',
      Authorization: `Bearer ${token}`,
      ...headers,
    },
    data: { ...newItem },
    method: 'POST',
  });

export const deleteSalaryFrequencyConfig = (id, token, headers = {}) =>
  request({
    prefix,
    url: `/SalaryFrequencyConfig/${id}`,
    headers: {
      Accept: 'application/json',
      'Content-Type': 'application/json',
      Authorization: `Bearer ${token}`,
      ...headers,
    },
    method: 'DELETE',
  });

export const getSalaryFrequencyConfigId = id =>
  request({
    prefix,
    url: `/SalaryFrequencyConfig/${id}`,
    method: 'GET',
  });

export const updateSalaryFrequencyConfig = (id, editDoc, token, headers = {}) =>
  request({
    prefix,
    url: `/SalaryFrequencyConfig/${id}`,
    data: { ...editDoc },
    headers: {
      Accept: 'application/json',
      'Content-Type': 'application/json',
      Authorization: `Bearer ${token}`,
      ...headers,
    },
    method: 'PUT',
  });

export const downloadFile = (paramsSearch, token, headers = {}) =>
  requestDownload({
    prefix,
    defaultFileName: 'ExportSalaryFrequencyConfigs.xlsx',
    url: `/SalaryFrequencyConfig/ExportSalaryFrequencyConfigs`,
    data: { ...paramsSearch },
    headers: {
      Accept: 'application/json',
      'Content-Type': 'application/json',
      Authorization: `Bearer ${token}`,
      ...headers,
    },
    method: 'POST',
  });



